import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { getDataSimple } from '../redux/actions/table'
import * as tableActions from '../redux/actions/table'
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import Button from '@material-ui/core/Button';
import { Link } from 'react-router-dom'
import QrReader from 'react-qr-reader';
import { mainWindow } from '../App'
const width = mainWindow.current.offsetWidth>800? 500: 240;
const maxWidth = mainWindow.current.offsetWidth>800? mainWindow.current.offsetWidth-240: mainWindow.current.offsetWidth;
const left = (maxWidth-width)/2;

const styles = theme => ({
    mainCam: {
        width: '100%',
        display: 'flex',
        justifyContent: 'center'
    },
    button: {
        margin: theme.spacing.unit,
    },
    textFieldSmall: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    urls: {
        margin: theme.spacing.unit,
        width: width,
        maxHeight: 100,
        overflow: 'auto'
    },
    cam: {
        width: '100%',
        maxWidth: 500,
        maxHeight: 500
    },
    message: {
        width: width,
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
    },
    text_message: {
        width: width,
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        overflow: 'hidden',
        wordWrap: 'break-word'
    },
    center: {
        width: width,
        marginLeft: left,
        marginRight: left,
    },
});

const Scanner = (
    (props) =>{
        useEffect(async()=>{
            if (!(status.status==='active'&&['admin', 'manager', 'cashier', 'cinema'].includes(status.role))) {
                props.history.push('/')
            }
        },[])
        const { setSelected, setData } = props.tableActions;
        const { status } = props.user;
        const { classes } = props;
        let [result, setResult] = useState(null);
        let [statusTicket, setStatusTicket] = useState('');
        let statusesTicket = ['зарезервирован', 'продан', 'возвращен', 'использован', 'отмена']
        let handleStatusTicket =  (event) => {
            setStatusTicket(event.target.value)
        };
        let handleScan = async (data) => {
            if (data) {
                data = await getDataSimple({name: 'БилетыКиноHash', data: {hash: data}})
                if(data!=undefined){
                    setStatusTicket(data.status)
                    setResult(data);
                }
            }
        }
        const cameraTypes = ['environment', 'user']
        let [cameraType, setCameraType] = useState('environment');
        let handleCameraType = (event) => {
            setCameraType(event.target.value);
        }
        let handleError = (err) => {
            console.log(err);
        }
        return (
            <div>
                {result===null?
                    <>
                    <br/>
                    <b>Сканирование</b>
                    <br/>
                    <br/>
                    <TextField
                        select
                        SelectProps={{
                            MenuProps: {
                                className: classes.menu,
                            },
                        }}
                        label='тип камеры'
                        type='login'
                        className={classes.textField}
                        margin='normal'
                        value={cameraType}
                        onChange={handleCameraType}
                    >
                        {cameraTypes.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>
                        ))}
                    </TextField>
                    <br/>
                    <div className={classes.mainCam}>
                        <QrReader
                            facingMode={cameraType}
                            onError={handleError}
                            onScan={handleScan}
                            className={classes.cam}
                        />
                    </div>
                    <br/>
                    </>
                    :
                    <div className={classes.center}>
                        <br/>
                        <a style={{fontWeight: 'bold'}} href={result.ticket} target='_blank'>Билет</a>
                        <br/>
                        <br/>
                        <b>Hash</b>
                        <br/>
                        {result.hash}
                        <br/>
                        <br/>
                        <b>Пользователь</b>
                        <br/>
                        {'имя: '+result.user.name+'; email: '+result.user.email+'; id:'+result.user._id+';'}
                        <br/>
                        <br/>
                        <b>Фильм</b>
                        <br/>
                        {result.movie}
                        <br/>
                        <br/>
                        <b>Тип</b>
                        <br/>
                        {result.type}
                        <br/>
                        <br/>
                        <b>Кинотеатр</b>
                        <br/>
                        {result.cinema}
                        <br/>
                        <br/>
                        <b>Зал</b>
                        <br/>
                        {result.hall}
                        <br/>
                        <br/>
                        <b>Места</b>
                        <br/>
                        {result.seats.length>0?
                            result.seats.map(option => {
                                return(
                                    <>
                                    {'Место: ' + option.name+' Цена: '+option.priceSelect+' Дата: '+option.date}
                                    <br/>
                                    </>)
                            }):null}
                        <br/>
                        <TextField
                            select
                            SelectProps={{
                                MenuProps: {
                                    className: classes.menu,
                                },
                            }}
                            label='статус'
                            type='login'
                            className={classes.textField}
                            margin='normal'
                            value={statusTicket}
                            onChange={handleStatusTicket}
                        >
                            {statusesTicket != undefined?
                                statusesTicket.map(option => (
                                    <MenuItem key={option} value={option}>
                                        {option}
                                    </MenuItem>
                                ))
                                :
                                null
                            }
                        </TextField>
                        <br/>
                        <div>
                            <Button variant='contained' color='primary' className={classes.button} onClick={()=>{
                                setData({id: result._id, search: '', sort: '', page: 0, name: 'Билеты', data: {status: statusTicket}});
                                setSelected(-1)
                                setResult(null)
                            }}>
                                Сохранить
                            </Button>
                            <Button variant='contained' color='secondary' className={classes.button} onClick={()=>{setResult(null)}}>
                                Заново
                            </Button>
                        </div>
                    </div>
                }
            </div>
        );
  })

function mapStateToProps (state) {
    return {
        table: state.table,
        user: state.user,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        tableActions: bindActionCreators(tableActions, dispatch),
    }
}

Scanner.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Scanner));
